/* toggleBtn
------------------------------------------------------------------------------*/

.toggleBtn {
  position: fixed;
  top: 0.5rem;
  right: 0.5rem;
  padding: 8px 0;
  cursor: pointer;
  z-index: 99;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.toggleBtn span {
  display: block;
  width: 0.5rem;
  height: 4px;
  background-color: #000000;
  position: relative;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.toggleBtn.open span {
  background-color: #ffffff;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.toggleBtn.open span:before {
  background-color: #ffffff;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  bottom: 0;
}

.toggleBtn.open span:after {
  display: none;
}

.toggleBtn span:before {
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  background-color: #000000;
  position: absolute;
  left: 0;
  bottom: calc(100% + 4px);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.toggleBtn span:after {
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  background-color: #000000;
  position: absolute;
  left: 0;
  top: calc(100% + 4px);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.mask {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  min-height: calc(500px + 1rem);
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -5;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  opacity: 0;
}

